/* ==========================================================================
   1. Common css start
   ========================================================================== */

@import url(' https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap');


body {
    font-family: $montserrat;
    font-weight: 400;
    font-style: normal;
    line-height: 1.2;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


img {
    vertical-align: middle;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
    text-decoration: none;
    outline: none;
}

a,
a:focus,
a:hover {
    text-decoration: none;
}

i,
span,
a {
    display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    margin: 0px;
}

h1 {
    font-weight: 700;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 22px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 16px;
}

ul,
ol {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}

.fluid-img {
    max-width: 100% !important;
}

.text-center {
    text-align: center;
}

p {
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    margin: 0;
    padding: 0;

    @include media-breakpoint-up(md) {
        font-size: 16px;
    }

}


.back-to-top {
    width: 40px;
    line-height: 40px;
    cursor: pointer;
    position: fixed;
    bottom: 50px;
    right: 0;
    background: #313131;
    box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.2);
    color: #fff;
    text-align: center;
    font-size: 40px;
    transition: all ease-out 0.2s;
    z-index: 999;

    &:hover {
        color: #ffb100;
    }
}

.common-text h3 {
    font-size: 24px;
    font-family: $kaushan;
    color: #000;
    margin-bottom: 10px;
    text-transform: capitalize;
    display: inline-block;
    position: relative;
    z-index: 2;

    &::after {
        content: "";
        position: absolute;
        width: calc(100% + 6px);
        height: 10px;
        background-color: #ffb100;
        top: 50%;
        left: -2px;

        display: block;
        z-index: -1;
    }
}


/* ==========================================================================
   Common css end
   ========================================================================== */