/* ==========================================================================
   4. Service css start
   ========================================================================== */

.service {
    background: #fff;

    .service-content {
        text-align: center;
        padding: 45px 25px;
        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
        background-color: rgba(255, 255, 255, 0.78);
        transition: linear all 0.2s;
        &:hover{
            transform: translateY(-5px);
        }

        .icon i {
            display: inline-block;
            font-size: 22px;
            font-family: Pe-icon-7-stroke;
            color: rgb(255, 177, 0);
            font-weight: 400;
            width: 60px;
            height: 60px;
            line-height: 60px;
            text-align: center;
            border: 2px solid rgba(0, 0, 0, 0.08);
            border-bottom-width: 3px;
            border-radius: 50%;
            margin-bottom: 25px;
            box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
        }

        h4 {
            font-size: 17px;
            font-weight: 600;
            color: #101010;
            margin-bottom: 15px;
        }
        p{
            margin-bottom: 0;
            font-size: 15px;
            line-height: 2.1;
        }
    }


}

/* ==========================================================================
   4. Service css end
   ========================================================================== */