/* ==========================================================================
   2. Header css start
   ========================================================================== */


$items: 4;
$transition-duration: 0.5s;
$transition-delay: 0.05s;

/* Basic Styles */

* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
}

body {
    font-family: $montserrat;
}



.sticky {
    background: #fff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    width: 100%;
    padding: 0;
    @include animation(sticky 1s);

    .nav-item {
        color: #333!important;

        &::after {
            background: rgba(255, 177, 0, 0.85) !important;
        }
    }
    .logo a{
        color: #333!important;
    }
    .nav-item:before{
        background-color: #333!important;
    }
}


header {
    width: 100%;
    position: fixed;
    z-index: 99;
    transition: all ease-in-out 0.3s;

    .mobile_menu {
        padding: 28px 0;

        .logo {
            a {
                font-size: 20px;
                color: #fff;
                text-transform: uppercase;
                font-weight: 700;
                span{
                    color: #ffb100;
                }
            }
        }

        .nav {
            .nav-item {
                font-size: 12px;
                font-weight: 400;
                text-transform: uppercase;
                padding: 0 5px;         
                transition: $transition-duration;
                position: relative;

                &::after {
                    content: "";
                    position: absolute;
                    bottom: -5px;
                    left: 50%;
                    right: 0;
                    height: 1px;
                    width: 50%;
                    background: rgba(255, 177, 0, 0.85);
                    pointer-events: none;
                    opacity: 0;
                    -webkit-transform: translateX(-50%);
                    transform: translateX(-50%);
                    transition: width .3s cubic-bezier(0.55, .085, .68, .53), opacity .2s cubic-bezier(0.55, .085, .68, .53);
                    transition: width .3s cubic-bezier(0.55, .085, .68, .53), opacity .2s cubic-bezier(0.55, .085, .68, .53);
                }

                &:hover::after {
                    width: calc(100% + 5px);
                    opacity: 1;
                    transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
                    transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
                }

                @include media-breakpoint-up(lg) {
                    color: #fff;
                    margin: 0 10px;
                    margin-right: 5px;
                }
                @include media-breakpoint-up(down) {
                    
                    margin-right: 5px;
                }
            }

            .active::after {

                width: calc(100% + 5px);
                opacity: 1;
                transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
                transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;

            }
        }
    }

    @include media-breakpoint-down(md) {
        .mobile_menu {
            position: relative;
            width: 100%;
            overflow: hidden;
        }


        #toggle {
            display: none; 
        }


        #toggle:checked {

            &~.toggle-container .button-toggle {
                box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);

                &:before {
                    transform: translateY(-50%) rotate(45deg) scale(1);
                }

                &:after {
                    transform: translateY(-50%) rotate(-45deg) scale(1);
                }
            }

            &~.nav {
                margin-bottom: 100px;
                pointer-events: auto;
                transform: translate(-100px, 50px);

                .nav-item {
                    color: #fff;
                    letter-spacing: 0;
                    height: 40px;
                    line-height: 40px;
                    margin-top: 0;
                    opacity: 1;
                    transform: scaleY(1);
                    transition: $transition-duration, opacity 0.1s;


                    @for $i from 1 through $items {
                        &:nth-child(#{$i}) {
                            $delay: ($items - $i) * $transition-delay;
                            transition-delay: $delay;

                            &:before {
                                transition-delay: $delay;
                            }
                        }
                    }
                    &:before {
                        opacity: 0;
                    }
                }
            }

            &~.dummy-content {
                padding-top: 30px;

                &:before {
                    background-color: rgba(0, 0, 0, 0.3);
                }
            }
        }

        .button-toggle {
            position: absolute;
            display: inline-block;
            width: 20px;
            height: 20px;
            margin-right: 10px;
            background-color: transparent;
            border: none;
            cursor: pointer;
            border-radius: 100%;
            transition: $transition-duration + 0.1;
            right: 0 !important;

            &:hover {
                box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1);
            }

            &:before,
            &:after {
                position: absolute;
                content: '';
                top: 50%;
                right: 0;
                width: 100%;
                height: 2px;
                background-color: #fff;
                border-radius: 5px;
                transition: $transition-duration;
            }

            &:before {
                transform: translateY(-50%) rotate(45deg) scale(0);
            }

            &:after {
                transform: translateY(50%) rotate(-45deg) scale(0);
            }
        }

        .nav {
            display: inline-block;
            pointer-events: none;
            transition: $transition-duration;
        }

        // Showing nav items as lines, making up the hamburger menu icon
        .nav-item {
            position: relative;
            float: right;
            clear: both;
            color: transparent;
            font-size: 14px;
            letter-spacing: -5.5px;
            height: 7px;
            line-height: 7px;
            text-transform: capitalize;
            white-space: nowrap;
            transform: scaleY(0.2);
            transition: $transition-duration, opacity 1s;

            @for $i from 1 through $items {
                &:nth-child(#{$i}) {
                    $delay: ($i - 1) * $transition-delay;
                    transition-delay: $delay;

                    &:before {
                        transition-delay: $delay;
                    }
                }
            }

            &:nth-child(1) {
                letter-spacing: -5px;
            }
            &:nth-child(2) {
                letter-spacing: -5px;
            }

            &:nth-child(n + 3) {
                letter-spacing: -8px;
                margin-top: -7px;
                opacity: 0;
            }

            &:before {
                position: absolute;
                content: '';
                top: 50%;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: #fff;
                transform: translateY(-50%) scaleY(5);
                transition: $transition-duration;
            }
        }
    }

}

@keyframes sticky {
    0% {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%)
    }

    100% {
        -webkit-transform: translateY(0%);
        transform: translateY(0%)
    }
}



/* ==========================================================================
   header css end
   ========================================================================== */