/* ==========================================================================
   5. Portfolio css start
   ========================================================================== */

.about {
    background: #fff;

    .follow {
        margin: 25px 0;

        ul {
            display: inline-block;

            li {
                display: inline-block;
                font-size: 14px;
                margin-bottom: 10px;
                color: #888;
                width: 100%;
                font-family: $montserrat;

                @include media-breakpoint-up(lg) {

                    width: 49%;

                }
            }
        }

        span {
            display: inline-block;
            position: relative;
            font-weight: 600;
            color: #333;
            width: 80px;
            margin-right: 5px;
            padding-right: 5px;

            &::after {
                content: ":";
                position: absolute;
                right: 0;
            }
        }

        a {
            display: inline-block;
            padding: 15px 26px;
            font-size: 18px;
            font-weight: 600;
            transition: all 0.3s ease-in-out;
            text-transform: uppercase;
            letter-spacing: 1px;
            border-radius: 0;
            background-color: transparent;
            border: 1px solid #ffb100;
            margin-top: 25px;
            color: #333;

            &:hover {
                background: #ffb100;
            }
        }
    }

    .skills {
        margin-top: 20px;

        .skill {
            margin-bottom: 30px;
            visibility: hidden;

            .progress_bg {
                background-color: rgba(0, 0, 0, 0.06);
            }

            .progress_bar {
                height: 5px;
                background-color: #ffb100;
            }

            span {
                font-size: 14px;
                font-weight: 600;
                color: #222;
                text-transform: uppercase;

                &:last-of-type {
                    float: right;
                }
            }
        }
    }

    .me {
        img {
            border-radius: 4%;
            max-height: 400px;
            box-shadow: 0 0 25px rgba(0, 0, 0, 0.45);

        }
    }
}




.skill-part {
    background: #fafafa;
}

/* ==========================================================================
5. portfolio css end
========================================================================== */