/* ==========================================================================
   3. Contact css start
   ========================================================================== */

.contact {
    background: #fff;

    .info {
        i {
            font-size: 23px;
            color: #fff;
            text-align: center;
            border-radius: 50%;
            top: 10px;
            width: 55px;
            height: 55px;
            line-height: 55px;
            background-color: #ffb100;
        }

        p {
            font-size: 15px;
            color: rgba(0, 0, 0, 0.55);
            margin-left: 15px;
        }
    }

    .control {
        width: 100%;
        margin-bottom: 25px;
        background: #f3f3f3;
        border: 1px solid rgba(0, 0, 0, 0.06);
        font-size: 15px;
        padding: 15px;
        transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

        &:focus {
            border-color: #ffb100;
        }
    }

    textarea.control {
        height: 150px;
    }

    button {
        padding: 15px 26px;
        display: inline-block;
        font-size: 10px;
        font-weight: 600;
        transition: all 0.3s ease-in-out;
        -webkit-transition: all 0.3s ease-in-out;
        text-transform: uppercase;
        letter-spacing: 1px;
        border-radius: 0;
        background-color: transparent;
        border: 1px solid #ffb100;

        &:hover {
            background: #ffb100;
        }
    }
}

/* ==========================================================================
   3. Contact css end
   ========================================================================== */