/* ==========================================================================
   3. Blog css start
   ========================================================================== */

.blog {
    background: #fafafa;

    .blog-iteam {
        background: #fff;

        img {
            transition: all 0.2s ease-in-out;
        }

        a {
            overflow: hidden;
            transform: scale(1);
        }

        &:hover img {
            transform: scale(1.2);
        }

        .blog-content {
            padding: 15px;
        }
    }

    .blog-date {
        display: block;
        padding-top: 28px;
        font-size: 14px;
        font-weight: 500;
        color: rgba(0, 0, 0, 0.55);
    }

    h4 {
        color: #040404;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 8px;
        line-height: 1.6;
        margin-top: 10px;
        text-transform: capitalize;
        -webkit-transition: all 0.2s ease-in-out;
        transition: all 0.2s ease-in-out;

        &:hover {
            opacity: 0.7;
        }
    }

    p {
        text-align: left;
        font-size: 15px;
        color: rgba(0, 0, 0, 0.55);
        line-height: 2.5;
    }
}

/* ==========================================================================
   3. Blog css end
   ========================================================================== */