/* ==========================================================================
   3. Hero css start
   ========================================================================== */




.hero {
    height: 100vh;
    width: 100%;
    position: relative;
    z-index: 1;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.85);
        z-index: -1;

    }

    .hero-content {
        h1 {
            font-size: 40px;
            color: #fff;
            font-family: $montserrat;
            text-transform: capitalize;
            margin-bottom: 25px;

            span {
                background: linear-gradient(to right, #fff0 -10%, #ffb100 100%);
            }

            @include media-breakpoint-up(sm) {

                font-size: 48px;

            }
        }

        .block h4 {
            padding: 10px;
            background: rgba(0, 0, 0, 0.61);
            display: inline-block;
            text-transform: uppercase;
            font-size: 16px;
            color: rgba(255, 255, 255, 0.81);
            letter-spacing: 4px;
            font-weight: 500;
            line-height: 1.4;

            @include media-breakpoint-up(sm) {

                padding: 15px 30px;

            }
        }

    }
}





/* ==========================================================================
   3. Hero css end
   ========================================================================== */