/* ==========================================================================
   5. Portfolio css start
   ========================================================================== */

#portfolio {
    background: #fafafa;

    .filter-part {
        button {
            padding: 5px;
            margin: 10px;
            border: none;
            background: transparent;
            text-transform: capitalize;
            font-size: 20px;
            cursor: pointer;
            outline: none;
            transition: all linear 0.2s;
            position: relative;
            color: #999;

            &:hover {
                color: #000;
            }

            &::after {
                content: '';
                position: absolute;
                bottom: -0px;
                left: 50%;
                right: 0;
                height: 1px;
                width: 50%;
                background: rgba(255, 177, 0, 0.85);
                pointer-events: none;
                opacity: 0;
                -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
                transition: width .3s cubic-bezier(0.55, .085, .68, .53), opacity .2s cubic-bezier(0.55, .085, .68, .53);
                transition: width .3s cubic-bezier(0.55, .085, .68, .53), opacity .2s cubic-bezier(0.55, .085, .68, .53);
            }

            &:hover::after {
                width: calc(100% + 5px);
                opacity: 1;
                transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
                transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
            }
        }

        .current {
            color: #000;

            &::after {
                width: calc(100% + 5px);
                opacity: 1;
                transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
                transition: width .3s cubic-bezier(0.25, .46, .45, .94), opacity 0s linear;
            }
        }

    }

    .grid-item {
        position: relative;
        overflow: hidden;
        margin-bottom: 20px;
        z-index: 1;
        width: 31.33%;
        box-sizing: border-box;

        img {
            -webkit-transform: scale(1.2);
            transform: scale(1.2);
            transition: all ease-in-out 0.2s;
        }

        &:hover img {
            -webkit-transform: scale(1.4);
            transform: scale(1.4);
        }
        
    }
}

@include media-breakpoint-down(sm) { 
    .grid-item{
        width: 100% !important;
    }
 }



.grid-item .img-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(43, 42, 47, 0.85);
    transform: scale(0);
    transition: all ease-in-out 0.2s;
    z-index: 1;
    
}

