.grid-item .img-overlay h3 {
    color: #fff;
    text-align: center;
    position: absolute;
    right: 10%;
    bottom: 20%;
    font-size: 18px;
    text-transform: capitalize;
    font-weight: 600;
    opacity: 1;
    letter-spacing: 2px;
}

.grid-item .img-overlay h3::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: -50%;
    width: 50%;
    height: 2px;
    background: #ffb100;
}

.grid-item:hover .img-overlay{
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
}









