/* ==========================================================================
   3. Footer css start
   ========================================================================== */

.footer {
    background: #141414;
    .social-link{
        i{
            color: #fff;
            padding: 10px;
            transition: all ease-in-out 0.2s;
            &:hover{
                color: #ffb100;
            }
        }
    }

    p {
        text-align: center;
        color: #999;
        font-size: 15px;
        font-weight: 400;

        a {
            color: #ffb100;
            font-weight: 600;
        }
    }
}

/* ==========================================================================
   3. Footer css end
   ========================================================================== */